/*
* Style Works for Editor Only
*/
.aab__label {
  font-size: 13px;
  text-transform: capitalize;
  font-weight: 400;
  line-height: 1.4;
}

.aab__sublabel {
  font-size: 12px;
  font-weight: 400;
  line-height: 1.4;
  margin-top: 4px;
  color: rgb(117, 117, 117);
}

.aa-custom-spacer {
  width: 100%;
  height: 1px;
  background: #e3e3e3;
  margin: 16px 0;
}

.block-editor-block-inspector .components-panel__body {
  &.is-opened {
    .aab-component-toolpanel {
      &:nth-child(2) {
        border-top: 0 !important;
      }
    }
  }
}

.aab-component-toolpanel {
  padding: 12px 0 !important;

  .components-grid {
    gap: 0;
  }

  .components-base-control__label {
    margin-bottom: 0;
  }

  .component-box-control__linked-button.components-button.has-icon {
    height: 20px;
  }
}


.bbpc-control-half {
  width: 50%;
  float: left;

  &.yes-btn {
    padding-right: 5px;
  }

  &.no-btn {
    padding-left: 5px;
  }
}

.count {
  background: rgba(175, 184, 193, 0.2);
  height: 22px;
  color: #838793;
  font-size: 12px;
  line-height: 22px;
  padding: 0 2px;
  transition: all 0.3s;
  min-width: 20px;
  border-radius: 2em;
  font-weight: 500;
}

.aab__accordion_container.wp-block-aab-accordion-block {
  .aab__accordion_body {
    .block-editor-block-list__layout {
      min-height: 35px;

      > :first-child {
        margin-top: 0 !important;
        padding: 0;
      }
    }
  }

  &.aab__accordion_disabled {
    pointer-events: inherit !important;
  }
}

.custom-css-notice {
  font-style: italic !important;
}

.components-tools-panel-item .components-h-stack {
  .components-input-control ~ .components-range-control {
    margin-top: 0;
  }
}

.components-h-stack {
  .components-unit-control ~ .components-range-control {
    margin-bottom: 15px;
  }
}

.components-panel__body-title + .aab-component-toolpanel {
  padding-top: 0 !important;
  border-top: 0;
}